body {
  background-color: rgb(244, 245, 249);
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

.slider {
  width: 1440px;
  height: 1080px;
  border: 1px solid rgba(66, 86, 122, 0.1);
  margin: auto;
  position: relative;
  font-family: "PT Sans";
  background-color: rgb(244, 245, 249);
}
.slider::after {
  content: "";
  background-color: rgba(66, 86, 122, 0.1);
  height: 1px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.slider::before {
  content: "";
  background-color: rgba(66, 86, 122, 0.1);
  height: 100%;
  width: 1px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.slider__title {
  --title-left: 80px;
  font-weight: 700;
  font-size: 56px;
  line-height: 120%;
  letter-spacing: 0;
  color: rgb(66, 86, 122);
  width: 353px;
  height: 134px;
  position: absolute;
  top: 170px;
  left: var(--title-left);
}
.slider__title::before {
  content: "";
  height: 120px;
  width: 5px;
  background-image: linear-gradient(rgb(56, 119, 238), rgb(239, 93, 168));
  position: absolute;
  left: calc(-2px - var(--title-left));
  top: 7px;
  bottom: 0;
  margin: auto;
}

.circle {
  width: 536px;
  height: 530px;
  position: absolute;
  inset: 0;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}
.circle::before {
  content: "";
  border: 1px solid rgba(66, 86, 122, 0.2);
  border-radius: 50%;
  width: 530px;
  height: 530px;
  position: absolute;
  inset: 0;
  margin: auto;
}
.circle__wrapper {
  width: 536px;
  height: 464px;
  position: relative;
}
.circle__wrapper .item.active,
.circle__wrapper .item:hover {
  --divCircle-size: 56px;
  width: var(--divCircle-size);
  height: var(--divCircle-size);
  border: 1px solid rgba(48, 62, 88, 0.5);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 20px;
  color: rgb(66, 86, 122);
  background-color: rgb(244, 245, 249);
  position: absolute;
  transition: 300ms, transform 500ms;
}
.circle__wrapper .item.active span,
.circle__wrapper .item:hover span {
  font-size: 20px;
}
.circle__wrapper .item.active h3 {
  position: absolute;
  font-weight: 700;
  font-size: 20px;
  left: 76px;
}
.circle__wrapper div:not(.active):hover h3 {
  font-size: 0;
}
.circle__wrapper .item {
  --divCircle-size: 6px;
  width: var(--divCircle-size);
  height: var(--divCircle-size);
  border: 1px solid rgb(66, 86, 122);
  background-color: rgb(66, 86, 122);
  border-radius: 50%;
  position: absolute;
  cursor: pointer;
  transition: 300ms, transform 500ms;
}
.circle__wrapper .item span {
  font-size: 0;
}
.circle__wrapper .item:nth-child(1) {
  top: calc(0% - var(--divCircle-size) / 2);
  left: calc(75% - var(--divCircle-size) / 2);
}
.circle__wrapper .item:nth-child(2) {
  top: calc(50% - var(--divCircle-size) / 2);
  left: calc(100% - var(--divCircle-size) / 2);
}
.circle__wrapper .item:nth-child(3) {
  top: calc(100% - var(--divCircle-size) / 2);
  left: calc(75% - var(--divCircle-size) / 2);
}
.circle__wrapper .item:nth-child(4) {
  top: calc(100% - var(--divCircle-size) / 2);
  left: calc(25% - var(--divCircle-size) / 2);
}
.circle__wrapper .item:nth-child(5) {
  top: calc(50% - var(--divCircle-size) / 2);
  left: calc(0% - var(--divCircle-size) / 2);
}
.circle__wrapper .item:nth-child(6) {
  top: calc(0% - var(--divCircle-size) / 2);
  left: calc(25% - var(--divCircle-size) / 2);
}

.slider__wrapper {
  position: relative;
  top: 400px;
}

.swiperPeriods__bullets, .swiperPeriods__title {
  display: none;
}

.swiperPeriods__control {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: space-between;
  width: 120px;
  height: 88px;
  margin-top: 197px;
  margin-left: 80px;
}
.swiperPeriods__control_pagination {
  width: 36px;
  height: 18px;
  font-weight: 400;
  flex-basis: 100%;
  font-family: "PT Sans";
  font-size: 14px;
  line-height: 100%;
  color: rgb(66, 86, 122);
}
.swiperPeriods__control .swiper-button-disabled {
  opacity: 0.5;
}
.swiperPeriods .swiper-slide {
  font-size: 200px;
  height: 160px;
  font-weight: 700;
  display: flex;
  justify-content: center;
  color: rgb(56, 119, 238);
}
.swiperPeriods .swiper-slide :last-child {
  color: rgb(239, 93, 168);
}
.swiperPeriods button {
  width: 50px;
  height: 50px;
  background: inherit;
  border-radius: 50%;
  border: 1px solid rgba(66, 86, 122, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}
.swiperPeriods button:not(.swiper-button-disabled):hover {
  background: rgb(255, 255, 255);
  cursor: pointer;
}

.swiper.swiperEvents {
  margin-top: 56px;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  padding: 0 40px;
  cursor: grabbing;
}
.swiper.swiperEvents .swiper-wrapper .swiper-slide {
  height: 135px;
  max-width: 400px;
  min-width: 320px;
}
.swiper.swiperEvents .swiper-button-disabled {
  visibility: hidden;
}
.swiper.swiperEvents .swiper-wrapper {
  width: calc(100% - 80px);
}
.swiper.swiperEvents button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 0;
  box-shadow: 0 0 15px rgba(56, 119, 238, 0.1);
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.swiper.swiperEvents h2 {
  font-weight: 400;
  font-size: 25px;
  line-height: 120%;
  color: rgb(56, 119, 238);
  font-family: "Bebas Neue";
  margin-bottom: 15px;
}
.swiper.swiperEvents p {
  font-family: "PT Sans";
  height: 90px;
  width: auto;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 0;
  line-height: 30px;
  color: rgb(66, 86, 122);
}

.button__wrapper {
  width: 80px;
  height: 100%;
  background-color: rgb(244, 245, 249);
  position: absolute;
  display: flex;
  align-items: center;
  cursor: default;
}
.button__wrapper:nth-child(2) {
  left: 0;
  z-index: 9999;
  justify-content: flex-end;
}
.button__wrapper:nth-child(3) {
  right: 0;
  z-index: 9999;
  justify-content: flex-start;
}

@media (orientation: portrait) and (max-width: 1280px) {
  .slider {
    border: 0;
    width: 100%;
    height: 100%;
  }
  .slider__wrapper {
    top: 163px;
  }
  .slider::before {
    display: none;
  }
  .slider::after {
    display: none;
  }
  .slider__title {
    width: 123px;
    height: 48px;
    font-size: 20px;
    font-weight: 700;
    line-height: 120%;
    top: 59px;
    left: 20px;
  }
  .slider__title::before {
    display: none;
  }
  .slider .swiperPeriods {
    height: -moz-max-content;
    height: max-content;
    overflow: visible;
    position: relative;
  }
  .slider .swiperPeriods .swiper-wrapper::after {
    content: "";
    display: flex;
    width: 280px;
    border-bottom: 1px solid rgb(199, 205, 217);
    position: absolute;
    inset: 130px 0 0 0;
    margin: auto;
  }
  .slider .swiperPeriods .swiper-slide {
    height: -moz-max-content;
    height: max-content;
  }
  .slider .swiperPeriods .swiper-slide span {
    font-size: 56px;
    font-weight: 700;
  }
  .slider .swiperPeriods .swiperPeriods__title {
    color: rgb(66, 86, 122);
    font-weight: 700;
    width: 280px;
    position: absolute;
    inset: 90px 0 0 0;
    margin: auto;
    display: flex;
  }
  .slider .swiperPeriods .swiperPeriods__bullets {
    height: 6px;
    width: 86px;
    display: flex;
    justify-content: space-between;
    position: absolute;
    inset: 367px 0 0 0;
    margin: auto;
  }
  .slider .swiperPeriods .swiperPeriods__bullets .bullet {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: rgb(66, 86, 122);
    list-style-type: none;
    opacity: 0.4;
  }
  .slider .swiperPeriods .swiperPeriods__bullets .bullet.active {
    opacity: 1;
  }
  .slider .swiperPeriods .swiperPeriods__control {
    position: absolute;
    top: 342px;
    left: 20px;
    margin: 0;
    width: 58px;
    height: 50px;
    font-size: 14px;
  }
  .slider .swiperPeriods .swiperPeriods__control button {
    width: 25px;
    height: 25px;
  }
  .slider .swiperPeriods .swiperPeriods__control button svg {
    width: 6px;
    height: 8px;
  }
  .slider .swiperEvents {
    height: 150px;
    top: 50px;
  }
  .slider .swiperEvents .swiper-wrapper {
    left: 20px;
    position: absolute;
  }
  .slider .swiperEvents .swiper-wrapper .swiper-slide {
    height: auto;
    width: 100%;
  }
  .slider .swiperEvents h2 {
    font-size: 16px;
    width: 100%;
  }
  .slider .swiperEvents p {
    font-size: 14px;
    width: 100%;
  }
  .button__wrapper {
    display: none;
  }
  .circle {
    display: none;
  }
}/*# sourceMappingURL=Style.css.map */